@import url('https://fonts.googleapis.com/css2?family=Baloo+Tamma+2&family=Roboto&display=swap');

div {
  font-family: 'Roboto', sans-serif;
}
body {
  margin: 0;
  padding: 0;
  font-size: 16px;
  overflow: hidden;
  box-sizing: border-box;
}

$taskItemTitleColor: rgba(15, 134, 179, 1);
$taskItemBorderColor: rgba(228, 228, 228, 1);
$taskItemBackgroundColor: rgba(255, 253, 204, 0.82);
$taskItemTextColor: gray;
$taskItemTitleDoneColor: gray;
$taskItemBackgroundColorDanger: rgba(255, 94, 82, 1);
$taskItemBackgroundColorWarning: rgba(249, 229, 44, 1);
$taskItemBackgroundColorNormal: rgba(243, 243, 240, 1);

#root {
  .container{
    display: grid;
    background-color: white;
    grid-template-areas:
      'title header header header'
      'side main main main';
    grid-template-rows: 10vh 90vh;
    grid-template-columns: minmax(280px, 1fr) 3fr;
    grid-gap: 1px;
    .back-font{
      display:none;
    }
    .cg-title{
      h3{
          grid-area: title;
          text-align: center;
      }
      
    }
    .cg-side-button{
      display:none;
      .arrow{
        border-top:15px solid transparent;
        border-bottom:15px solid transparent;
        border-left:15px solid #3a3a3a;
        border-right:15px solid transparent;
        width:0;
        height:0;
        margin-left: 5px;
        &.rotate{
          border-left:15px solid transparent;
          border-right:15px solid #3a3a3a;
          margin-left: -10px;
        }
      } 
    }
    .cg-container{
      background-color: rgba(204, 218, 222, 1);
      grid-area: side;
      display:grid;
      grid-template-rows: 10fr 1fr;
      grid-template-columns: minmax(250px, 100%);
      .cg-list{
        overflow-y: auto;
        .cg-item{
          margin: 10px;
          border: 1px solid whitesmoke;
          background-color: #cff1ff;
          padding: 10px;
          border-radius: 5px;
          // cursor: pointer;
          &.active{
            border: 4px solid #70a0d0;
            background-color: #95c0d6;
          }
        }
      }
      .cg-button{
        padding:7px;
        button{
          display: flex;
          justify-content: center;
          align-items: center;
          z-index: 50;
          height:100%;
          width: 100%;
          border-radius: 5px;
          background: rgba(51, 96, 255, 1);
          color: white;
          outline-style: revert;
          outline-width: thin;
          border-width: 0;
          font-size: 1.2em;
          transition: 0.3s;
          
          &:hover {
            box-shadow: 0px 0px 9px 2px #009aec;
          }
          &:active {
            background-color: #1b458c;
          }
        }
      }
    }
    .task-title{
      padding: 10px;
      margin: auto 40px;
      overflow: hidden;
      grid-area: header;
      background-color: rgba(250, 250, 250, 1);
    }
    .task-list{
      padding: 10px 0;
      grid-area: main;
      background-color: rgba(239, 248, 251, 1);
      display: grid;
      grid-template-columns: repeat(2,minmax(200px, 1fr));
      grid-template-rows: repeat(auto-fill,250px);
      width: 100%;
      overflow-y: auto;
      .task-add-button{
        position:fixed;
        z-index: 5;
        bottom:50px;
        right:50px;
        width: 65px;
        height: 65px;
        text-align: center;
        font-size:3.3em;
        color:white;
        border-radius: 50%;
        background-color: rgba(51, 96, 255, 1);
        box-shadow: 0 0 10px 0px rgba(51, 96, 255, 1);
        opacity: 0.3;
        &:hover{
          transition: 0.3s;
          opacity:1;
        }
      }
      .task-item{
        width:auto;
        position: relative;
        height: auto;
        border: 1px solid $taskItemBorderColor;
        background-color: $taskItemBackgroundColor;
        border-radius: 5px 20px 5px 5px;
        color: $taskItemTextColor;
        display: grid;
        grid-template-rows: 4fr 1fr;
        grid-gap: 1px;
        text-align: left;
        box-shadow: 0 0 10px 0px #d8d8d8;
        margin: 10px;
        padding:0.7em;
        cursor: pointer;
        &.danger::after, &.warning::after, &.normal::after{
          position:absolute;
          content: '';
          width:30px;
          height:30px;
          border-radius: 50%;
          right:10px;
          bottom:10px;
        }
        &.danger::after{
          background-color: $taskItemBackgroundColorDanger;
        }
        &.warning::after{
          background-color: $taskItemBackgroundColorWarning;
        }
        &.normal::after{
          background-color: $taskItemBackgroundColorNormal;
        }
        p{
          padding:0;
          margin:0;
        }
        p:nth-child(1) {
          overflow-y: auto;
          color: $taskItemTitleColor;
          font-size: 1em;
        }
        p:nth-child(2) {
          font-size: 0.7em;
          border-top: 1px solid $taskItemBorderColor;
          padding-top: 10px;
          font-style: italic;
        }
        p:nth-child(3) {
          font-size: 0.7em;
          // padding-top: 10px;
          font-style: italic;
        }
        &.done{
          background-color: white;
          opacity: 0.3;
          p:nth-child(1){
            text-decoration-line: line-through;
            color: $taskItemTitleDoneColor;
          }
        }
        &:hover{
        }
        animation-name: fadeIn;
        animation-duration: 0.5s;
      }
    }
    @media screen and (max-width: 500px) {
      .cg-side-button{
        display:flex;
        align-items: center;
        position: fixed;
        top:50vh;
        left: 0;
        width: 25px;
        height: 80px;
        background-color: rgba(228, 241, 241, 0.4);
        border-radius: 0 20px 20px 0;
        // box-shadow: 0 0 5px black;
        border: 1px solid #9a9797;
        z-index: 80;
        transition: 0.5s;
        &:hover{
          box-shadow: 0 0 20px black;
        }
      }
      .back-font{
        display:block;
        position:fixed;
        top:0;
        left:0;
        width:100vw;
        height: 100vh;
        background-color: rgba(0, 0, 0, 0.7);
        z-index: 10;
        &.fadeout{
          transform:translate(-100vw);
          opacity: 0;
          animation-name: fadeOut;
          animation-duration: 0.3s;
        }
        &.fadein{
          opacity:1;
          animation-name: fadeIn;
          animation-duration: 0.5s;
        }
      }
      grid-template-areas: 'header' 'main';
      grid-template-columns: 1fr;
      grid-template-rows: 10vh 90vh; 
      .cg-title{
        display:none;
      }
      .cg-container{
        position:fixed;
        top:0;
        left:0;
        width:300px;
        height:100vh;
        z-index:50;
        &.show{
          animation-name: slideIn;
          animation-duration: 0.3s;
        }
        &.hide{
          transform:translateX(-300px);
          opacity:0;
          animation-name: slideOut;
          animation-duration: 0.3s;
        }
        
      }
    }
  }
}  

@keyframes fadeIn {
  0% {opacity: 0;}
  100% {opacity: 1;}
}
@keyframes fadeOut{
  0%{opacity: 1; transform:translate(0);}
  100%{opacity: 0; transform:translate(0);}
}
@keyframes slideIn{
  0%{transform:translateX(-300px); opacity:0;}
  100%{opacity:1;}
}
@keyframes slideOut{
  0%{transform:translateX(0); opacity:1;}
  100%{transform:translateX(-300px); opacity:1;}
}